.header {
    background-color: none;
    font-size: 25px;
    text-align: center;
    font-weight: bold;
    padding: 10px;
    margin-top: 30px;
    margin-bottom: 50px;
    color: rgb(229, 229, 232);

}

/* Media Query for Mobile Devices */
@media (max-width: 600px) {
    .header {
        font-size: 20px;
        margin-top: 20px;
        margin-bottom: 30px;
    }
}