.list {
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 500px;
    margin: 0 auto;
    padding: 20px;
    margin-top: 50px;
    max-height: 30vh;
    /* Set max height to allow scrolling */
    overflow-y: auto;
}

/* Media Query for Mobile Devices */
@media (max-width: 600px) {
    .list {
        padding: 10px;
        margin-top: 60px;
        width: auto;
        margin-left:  20px;
        margin-right: 20px;
    }
}