.todoform {
    background-color: white;
    border-radius: 8px;
    padding: 20px;
    width: 500px;
    margin-top: 50px;
    margin: 0 auto;
    box-shadow: 0 4px 6px rgb(0, 0, 0, 0.1);

}

.modernInput {
    width: 100%;
    border: none;
    display: block;
    color: black;
    font-size: 16px;
    padding: 7px;
    border-radius: 5px;
    transition: border-color 0.4s, box-shadow 0.4s;
    /* Smooth transition */

}

.modernInput:hover {
    border-color: rgba(0, 59, 186, 0.5);
    /* Change border color on hover */
    box-shadow: 0 0 7px rgba(0, 59, 186, 0.5);
    /* Add shadow for more effect */
}

.modernInput:focus {
    outline: none;
}

.modernButton {
    display: inline-block;
    border: none;
    border-radius: 4px;
    background-color: #1a52de;
    width: 100px;
    padding: 10px;
    color: #ffffff;
    cursor: pointer;

}

.inputContainer {
    display: flex;
}

/* Media Query for Mobile Devices */
@media (max-width: 600px) {
    .todoform {
        padding: 8px;
       width: 230px;
    }

    .inputContainer {
        flex-direction:column;
   
       
    }

    .modernButton {
        width: 40%;
        margin-top: 10px;
       margin-left: 70px;  
    }
    .modernInput{
        width: 210px;
    }
}