.itemName {
  font-weight: bold;
  padding: 15px;
  margin-right: 90px;

}

.item {
  position: relative;
  border: 1px solid #131010;
  padding: 1px;
  margin-bottom: 13px;
  border-radius: 3px;

}

.deleteButton {
  position: absolute;
  top: 19px;
  /* Adjust top positioning */
  right: 8px;
  /* Adjust right positioning */
  cursor: pointer;
  color: rgb(63, 61, 61);
  /* Example: Change color as needed */
}


.completed {
  text-decoration: line-through;
  color: rgb(14, 124, 208);
}

.itemContent {
  display: flex;
  align-items: center;
}


.itemContent input[type="checkbox"] {
  margin-left: 10px;
  margin-right: 10px;
  transform: scale(1.4);
  margin-right: 3px;
  color: #ccc;
  /* Adjust spacing between checkbox and item name */
}

.itemName {
  flex: 1;
  /* Take remaining space */
}

/* Media Query for Mobile Devices */
@media (max-width: 600px) {
  .itemName {
      font-size: 16px;
      padding: 10px;
      margin-right: 20px;
      width: 50px;
  }

  .deleteButton {
      top: 10px;
      right: 5px;
  }

  .itemContent input[type="checkbox"] {
      transform: scale(1.2);
  }
}