.footer {
  position: fixed;
  bottom: 0;
  font-size: 1.5em;
  font-weight: bold;
  text-align: center;
  width: 100%;
  background-color: white;
  padding: 10px;
  /* left: 365px;  */
  border-radius: 5px;
}

.item {
  margin: 20px;
  font-size: 20px;
  font-family: sans-serif;
  color: rgb(47, 46, 46);

}

.thumbsUp {
  margin-left: 5px;
  /* Adjust the spacing as needed */
  color: blue;
  /* Example: Change color as needed */
  font-size: 20px;
  /* Adjust size as needed */
}

.task {
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  color: rgb(47, 46, 46);
}

/* Media Query for Mobile Devices */
@media (max-width: 600px) {
  .footer {
      font-size: 1em;
      padding: 10px;
      flex-direction: column; /* Stack items vertically */
    
  }

  .item {
      margin: 10px;
      font-size: 16px;
    
  }

  .thumbsUp {
      font-size: 16px;
      
  }

  .task {
      font-size: 16px;
      
      
  }

  
}